<template>
  <v-container fluid>
    <v-card height="650">
      <v-toolbar flat color="primary" dark>
        <v-toolbar-title>User Profile</v-toolbar-title>
      </v-toolbar>
      <v-tabs >
        <v-tab>
          <v-icon left>mdi-calendar</v-icon>
          Daily
        </v-tab>
        <v-tab>
          <v-icon left>mdi-calendar</v-icon>
          Weekly
        </v-tab>
        <v-tab>
          <v-icon left>mdi-calendar</v-icon>
          Monthly
        </v-tab>
        <v-tab>
          <v-icon left>mdi-calendar</v-icon>
          Yearly
        </v-tab>
        <v-tab>
          <v-icon left>mdi-calendar</v-icon>
          DatePicker
        </v-tab>
        <v-tab>
          <v-icon left>mdi-calendar</v-icon>
          BOR
        </v-tab>

        <v-tab-item>
          <v-card flat>
            <div class="container" >
              <line-chart
                v-if="loaded"
                :chartData="downloads"
                :chartLabels="labels"
              />
            </div>
          </v-card>
        </v-tab-item>

        <v-tab-item>
          <v-card flat>
            <div class="container" >
              <line-chart
                v-if="loaded"
                :chartData="downloadsWeek"
                :chartLabels="labelsWeek"
              />
            </div>
          </v-card>
        </v-tab-item>

        <v-tab-item>
          <v-card flat>
            <div class="container" >
              <line-chart
                v-if="loaded"
                :chartData="downloadsMonth"
                :chartLabels="labelsMonth"
              />
            </div>
          </v-card>
        </v-tab-item>

        <v-tab-item>
          <v-card flat>
            <div class="container" >
              <bar-chart
                v-if="loaded"
                :chartData="downloadsYear"
                :chartLabels="labelsYear"
              />
            </div>
          </v-card>
        </v-tab-item>

        <v-tab-item>
          <v-card flat>
            <v-row>
              <v-col cols="12" sm="6" md="4" >
                <v-menu
                  v-model="menu"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="100px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="date"
                      label="Start Date"
                      prepend-icon="mdi-event"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker v-model="date" @input="menu = false" no-title scrollable></v-date-picker>
                </v-menu>
              </v-col>

              <v-col cols="12" sm="6" md="4" >
                <v-menu
                  v-model="menu2"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="100px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="date2"
                      label="End Date"
                      prepend-icon="mdi-event"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker v-model="date2" @input="menu2 = false" no-title scrollable></v-date-picker>
                </v-menu>
              </v-col>

              <v-btn v-on:click="greet">GO</v-btn>
            </v-row>
            <div class="container" >
              <line-chart
                v-if="loaded"
                :chartData="downloads"
                :chartLabels="labels"
              />
            </div>
          </v-card>
        </v-tab-item>

        <v-tab-item>
          <v-card flat>
            <div class="container" >
              <line-chart
                v-if="loaded"
                :chartData="totalBor"
                :chartLabels="statDate"
              />
            </div>
          </v-card>
        </v-tab-item>
        
      </v-tabs>
    </v-card>
  </v-container>
</template>

<script>
  // import axios from 'axios'
  import LineChart from '@/components/LineChart'
  import BarChart from '@/components/BarChart'

  import {
    dateToYear,
    dateToMonth,
    dateToWeek,
    dateToDay,
    dateBeautify
  } from '@/utils/dateFormatter'
  
  import { removeDuplicate, groupData } from '@/utils/downloadFormatter'

  export default {
    components: {
      LineChart,
      BarChart,
    },
    props: {},
    data () {
      return {
        package: 'moment',
        // period: 'last-year',
        loaded: false,
        downloads: [],
        downloadsWeek: [],
        downloadsMonth: [],
        downloadsYear: [],
        labels: [],
        labelsWeek: [],
        labelsMonth: [],
        labelsYear: [],
        showError: false,
        date: '',
        date2: new Date().toISOString().substr(0, 10),
        menu: false,
        menu2: false,
        totalBor: [],
        statDate: [],
      }
    },
    mounted () {
        this.requestData(),
        this.requestData2()
    },
    computed: {
      _endDate () {
        return dateToDay(this.date2)
      },
      _startDate () {
        return dateToDay(this.date)
      },
      period () {
        return this.date ? `${this._startDate}:${this._endDate}` : 'last-month'
      },
      formattedPeriod () {
        return this.date ? `${dateBeautify(this._startDate)} - ${dateBeautify(this._endDate)}` : 'last-month'
      }
    },

    methods: {
      resetState () {
        this.loaded = false
        this.showError = false
      },
      requestData () {
        this.resetState()
        this.loading = true
        this.$http.get(`https://api.npmjs.org/downloads/range/${this.period}/${this.package}`)
          .then(response => {
            this.rawData = response.data.downloads
            this.downloads = response.data.downloads.map(entry => entry.downloads)
            this.labels = response.data.downloads.map(entry => entry.day)
            this.packageName = response.data.package
            this.totalDownloads = this.downloads.reduce((total, download) => total + download)
            this.groupDataByDate()
            this.loaded = true
            this.loading = false
          })
          .catch(err => {
            this.errorMessage = err.response.data.error
            this.showError = true
            this.loading = false
          })
      },
      greet () {
        // `this` inside methods points to the Vue instance
        alert(this.period);
        // `event` is the native DOM event
        this.requestData()
        this.groupDataByDate ()
      },
      groupDataByDate () {
        this.formatYear()
        this.formatMonth()
        this.formatWeek()
      },
      formatYear () {
        this.labelsYear = this.rawData
          .map(entry => dateToYear(entry.day))
          .reduce(removeDuplicate, [])
        this.downloadsYear = groupData(this.rawData, dateToYear)
      },
      formatMonth () {
        this.labelsMonth = this.rawData
          .map(entry => dateToMonth(entry.day))
          .reduce(removeDuplicate, [])
        this.downloadsMonth = groupData(this.rawData, dateToMonth)
      },
      formatWeek () {
        this.labelsWeek = this.rawData
          .map(entry => dateToWeek(entry.day))
          .reduce(removeDuplicate, [])
        this.downloadsWeek = groupData(this.rawData, dateToWeek)
      },
      requestData2() {
        this.resetState()
        this.loading = true
        this.$http.get("http://kpjhqawebslb/hits2api/hitsdashboard/api/bor-daily")
          .then(response => {
            this.totalBor = response.data.map(entry => entry.TotalBOR)
            this.statDate = response.data.map(entry => entry.StatDate)
            this.loaded = true
            this.loading = false

            // console.log(this.downloads);
            // console.log(this.labels);
          })
          .catch(err => {
            this.errorMessage = err.response.data.error
            this.showError = true
            this.loading = false
          })
      }
    }
  }
</script>