<script>
  import { Line } from 'vue-chartjs'

  export default {
    extends: Line,
    props: {
      chartData: {
        type: Array || Object,
        required: false
      },
      chartLabels: {
        type: Array,
        required: true
      },
      legend: {
        type: String,
        required: true
      },
      legendColor: {
        type: String,
        required: true
      }
    },
    data () {
      return {
        options: {
          scales: {
            yAxes: [{
              ticks: {
                beginAtZero: true
              },
              gridLines: {
                display: true
              },
              scaleLabel: {
                display: true,
                labelString: "Percentage %"
              },
            }],
            xAxes: [ {
              gridLines: {
                display: false
              },
              scaleLabel: {
                display: true,
                labelString: "Date"
              }
            }]
          },
          legend: {
            display: true
          },
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            datalabels: {
              anchor: 'end',
              align: 'top',
              formatter: Math.round(2),
              font: {
                weight: 'bold'
              }
            }
          }
        }
      }
    },
    mounted () {
      this.renderChart({
        labels: this.chartLabels,
        datasets: [
          {
            label: this.legend[0],
            borderColor: this.legendColor,
            pointBackgroundColor: 'white',
            borderWidth: 1,
            pointBorderColor: this.legendColor,
            backgroundColor: 'transparent',
            data: this.chartData
          }
        ]
      }, this.options)
    }
  }
</script>